import VueRouter from 'vue-router';
import Vue from 'vue';
import DeviceList from '../pages/DeviceList'
import ComponyDeviceList from '../pages/ComponyDeviceList'
import ProjectList from '../pages/ProjectList'
import ProjectInfo from '../pages/ProjectInfo'


Vue.use(VueRouter);

const routes = [
    { path: '/', component: DeviceList },
    { path: '/ComponyDeviceList', component: ComponyDeviceList },
    { path: '/ProjectList', component: ProjectList },
    { path: '/ProjectInfo', component: ProjectInfo },

]
const router = new VueRouter({
    // history: VueRouter.createWebHashHistory(),
    routes,
    mode: 'hash'
})

export default router;
